.background_chat_main
{
    background-color: #e6e6e8;
    height: 90vh !important;
    overflow-y: hidden;
}

.height_of_chat
{
    height: 93vh;
    border-radius: 10px;
    overflow-y: auto;
}

.height_of_chat2
{
    height: 88vh;
    position: relative;
    z-index: 1;
}

.height_of_chat3
{
    background-color: white;
    height: 88vh;
    position: relative;
    z-index: 1;
    border: 1px solid #72DEEF;
    border-radius: 10px;
}

.height_of_chat_pic
{
    height: 50px;
    border-radius: 10px;
}

.input_bottom
{
    position: absolute;
    bottom: 14px;
    width: 97%;
    border-radius: 10px;
}

.background_chat_head
{
    background-color: #dadadc;
    border-radius: 10px;
}

.name_person
{
    font-size: 14px;
    font-weight: 600;
}

.name_person1
{
    font-size: 12px;
    font-weight: 600;
    color: gray;
}

.box_shadow_ofchat
{
    box-shadow: 1px 1px 7px 7px rgba(211, 211, 211, 0.291);
    border-radius: 10px;
}

#style-3::-webkit-scrollbar-track
{
	-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
    border-radius: 10px;
	background-color: #F5F5F5;
}

#style-3::-webkit-scrollbar
{
	width: 6px;
    border-radius: 10px;
	background-color: #F5F5F5;
}

#style-3::-webkit-scrollbar-thumb
{
    border-radius: 10px;
	background-color: #72DEEF;
}

.img_height_width
{
    height: 70px;
    width: 70px;
    object-fit: cover;
}

.universal_cursor
{
    cursor: pointer;
}

.height_of_msg
{
    height: 50vh;
    overflow-y: auto;
    overflow-x: hidden;
}

.color_blue_text
{
    color: white;
    background-color: #2680EB !important;
    border-radius: 10px;
    
}

.color_blue_text1
{
    color: black;
    background-color:  white;
    border-radius: 10px;
}

.new_place_holder
{
    border: 1px solid #2680EB;
}